
import IconCustom from "@/components/common/icon-custom/IconCustom.vue";

export default {
	name: "ProgressCardItem",
	components: { IconCustom },
	props: {
		/** Значение */
		value: Number,
		/** Заголовок */
		title: String,
		/** Описание */
		description: String,
		/** Цвет прогресса */
		color: {
			type: String,
			default: "success"
		},
		/** Размер иконок */
		size: {
			type: Number,
			default: 44
		},
		/** Нет данных */
		nodata: Boolean,
		/** Выкл. */
		disabled: Boolean
	}
};
