
import { computed, toRefs } from "vue";

export default {
	name: "SwitchCardItem",
	props: {
		/** Значение switch */
		modelValue: Boolean,
		/** Заголовок */
		title: String,
		/** Описание */
		description: String,
		/** Цвет switch */
		color: {
			type: String,
			default: "success"
		},
		/** Нет данных */
		nodata: Boolean,
		/** Выкл. */
		disabled: Boolean
	},
	emits: ["update:modelValue"],
	setup(props: any, { emit }: { emit: any })
	{
		const { modelValue } = toRefs(props);
		const modelSwitch = computed({
			get: () => modelValue.value,
			set: (val) => emit("update:modelValue", val)
		});

		return {
			modelSwitch
		};
	}
};
